html {
  height: 100%;
}
body {
  background: #fc00ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #fc00ff,
    #00dbde
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#fc00ff), to(#00dbde));
  background: -webkit-linear-gradient(left, #fc00ff, #00dbde);
  background: -o-linear-gradient(left, #fc00ff, #00dbde);
  background: linear-gradient(to right, #fc00ff, #00dbde);
  font-family: "Montserrat", sans-serif;
  color: #fff;
  min-height: 100%;
}
h1 {
  font-family: "Oswald", sans-serif;
}
h2 {
  font-size: 36px;
  line-height: 40px;
}
h3 {
  font-weight: 300;
}
.App {
  margin: 0 auto;
  width: 980px;
  padding: 160px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.Preview {
  -ms-flex: 0 1;
      flex: 0 1;
  -ms-flex-positive: 0;
      flex-grow: 0;
}
code {
  display: block;
  background: rgba(255, 255, 255, 0.3);
  padding: 20px;
  color: rgba(24, 24, 24, 0.9);
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 20px;
}

.PreviewBox {
  background: #fff;
  padding: 10px;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.07), 0 2px 5px rgba(0, 0, 0, 0.03);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0.07), 0 2px 5px rgba(0, 0, 0, 0.03);
}

.Description {
  padding: 0 40px;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

@media (max-width: 940px) {
  .App {
    display: block;
    width: 100%;
  }

  .Preview {
    -ms-flex-item-align: center;
        align-self: center;
    margin: 0 auto;
    width: 380px;
  }
}



